	module.exports = {"data":{"post":{"html":"<p>As promised, I'm in the back row at BlogTalk, wearing my dead fish cap, next to a power outlet so I can fire up my little ThinkPad. And the WiFi seems to work. </p>\n<p>I'm skeptical of most of the points brought up by the panelists so far. How can bloggers internationally maintain any sort of meaningful dialouge given the wide cultural and language differences? How can any kind of semantic markup keep up with the dynamic interests and specialties of the people who write the blogs? And why don't they have any coffee at this conference center?</p>\n<p>By the way, both Heiko and Haiko are pronounced HIGH-ko, but they are easy to tell apart. Heiko is the one who can't read the map, and Haiko is the one who <s>can't find</s> never uses the brakes.</p>","fields":{"title":"Sitting In The Back Row","date":"23 May 2003","url":"https://papascott.de/2003-05-23-sitting-in-the-back-row/"}}},"pathContext":{"slug":"/2003-05-23-sitting-in-the-back-row/","prev":{"fields":{"title":"Blogging Around the World","date":"23 May 2003","slug":"/2003-05-23-blogging-around-the-world/"}},"next":{"fields":{"title":"[BlogTalk] Updated List of Conference Bloggers","date":"24 May 2003","slug":"/2003-05-24-blogtalk-updated-list-of-conference-bloggers/"}}}}

